<template>
  <div id="app">
    <Header />
    <projects />
    <Blog />
    <Footer />
  </div>
</template>

<script>
import Header from './components/Header.vue'
import Footer from './components/Footer.vue'
import Projects from './components/Projects.vue'
import Blog from './components/Blog.vue'

export default {
  name: 'app',
  components: {
    Header,
    Projects,
    Blog,
    Footer
  }
}
</script>

<style>
body {
  /* background-color: #f6f8f8; */
  margin: 0px;
  border-top:  6px solid #121111;
  border-bottom:  4px solid #121111;
  height: 100%;
}
#app {
  font-family: 'Neucha','Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #121111;
  padding-top: 60px;
  align-self: center;
  max-width: 580px;
  margin-right: auto;
  margin-left: auto;
}

a {
  color: #42b983;
  text-decoration: none;
}
</style>



