<template>
  <div id="projects">
    <h2>PROJECTS</h2>
    <img v-if="projects.length == 0" src="./../assets/no_projects.jpg">
    <div class="project">
      <ul>
        <li v-scroll-reveal={delay:250} v-for="project in projects" 
        v-bind:key="project.id">
          <!-- <img v-bind:src="project.image" v-bind:alt="project.title"> -->
          <div>
            <h3><a v-bind:href="project.link">{{project.title}}</a></h3>
            <p class="project_desc">
              {{project.desc}}
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import data from '../data.js'

export default {
  name: "Projects",

  data() {
    return { projects: data.projects };
  }
};
</script>

<style scoped>
h3 {
  margin: 20px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
  
}
li {
  display: flex;
  margin: 0 10px;
  text-align: left;
}

#projects {
  border-bottom: 1px solid #ebeaea;
  padding: 10px;
}

.project_desc{
  color: #615b5b;
  font-family: 'Lato', monospace;
}
</style>



