<template>
  <div id="header">
    <img v-bind:src="headerAvatar">
    <transition name="fade">
      <h1 id="header-title">
        {{ headerTitle }}
      </h1>
      </transition>
    <h5 id="header-info">
      {{ headerInfo }}
    </h5>
    
    <ul>
      <li><a v-bind:href="githubLink" target="_blank">Github</a></li>
      <span class="article-list-divider">-</span>
      <li><a v-bind:href="resumeLink" target="_blank">Resume</a></li>
      <span class="article-list-divider">-</span>
      <li><a v-bind:href="email" target="_blank">Email</a></li>
    </ul>
    
  </div>
</template>

<script>
import data from '../data.js'
export default {
  name: "Header",
  data() {
    return {
      headerTitle: data.info.name,
      headerInfo: data.info.job,
      headerAvatar: data.info.avatar_url,
      githubLink: "https://www.github.com/" + data.info.github_username,
      resumeLink: data.info.resume_url,
      email: data.info.email
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img{
  width: 100px;
  border-radius: 50%;
}
#header {
  border-bottom: 1px solid #ebeaea;
  background-color: white;
}
#header-title {
  font-family: 'Cormorant Garamond','Courier New', Courier, monospace;
  margin: 0px;
  font-size: 40px;
}
#header-info{
  font-family: 'Neucha','Courier New', Courier, monospace;
  margin: 0px;
  font-size: 22px;
  color: #636e72;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #289262;
  text-decoration: none;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



