<template>
  <div id="blog">
    <h2>BLOG</h2>
    <div class="post">
      <ul>
        <li v-scroll-reveal={delay:250} v-for="post in posts" 
        v-bind:key="post.id">
        <div class="post-container">
          <h3 class="post-title"><a href="">{{post.title}}</a></h3>
          <p class="post-desc">
            {{post.desc}}
          </p>
          </div>
          <div class="post-footer">
            <span class="post-date">
              January 19, 2018
            </span>
            <span class="post-divider">-</span>
            <span class="post-minutes">
              1 minute read
            </span>
            <span class="post-divider">-</span>
            <div class="post-tags">
              
            </div>
          </div>
        </li>

      </ul>
    </div>
  </div>
</template>


<script>
var data = [
  {
    title: "Me in the arymy ",
    desc: "this is me in the army and my army in me",
  }
];

export default {
  name: "Blog",

  data() {
    return { posts: data };
  }
};
</script>

<style scoped>
h3 {
  margin: 20px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
}
li {
  margin: 0 10px;
  text-align: left;
}
a {
  color: #42b983;
}
.post {
  font-family: "Lato", monospace;
}
.post-title {
}
.post-desc {
  line-height: 1.8;
  color: #615b5b;
  margin: 8px 0px;
}
.post-footer span,
.post-footer a {
  display: inline-block;
  font-size: 12px;
  color: #ada8a8;
  text-transform: uppercase;
  vertical-align: middle;
}

#blog {
  border-bottom: 1px solid #ebeaea;
  padding: 10px;
  display: none;
}
</style>



